import React from "react";
import { useParams } from "react-router-dom";
import { Product } from "../../types";
import { fetchProducts } from "../../api/api";
import "./ProductPage.scss";
import { Breadcrumbs } from "../../components/Breadcrumbs";

export const ProductPage: React.FC = () => {
  const { id } = useParams(); //  id  URL
  const [product, setProduct] = React.useState<Product | null>(null);
  const [loading, setLoading] = React.useState(true);

  React.useEffect(() => {
    const loadProduct = async () => {
      console.log(" ...");
      const products = await fetchProducts(); //  

      console.log(" :", products);
      console.log("   id:", id);

      const foundProduct = products.find((p) => p.id === Number(id));

      if (foundProduct) {
        console.log(" :", foundProduct);
        setProduct(foundProduct);
      } else {
        console.error("   !");
      }

      setLoading(false);
    };

    loadProduct();
  }, [id]);

  if (loading) return <p>...</p>;
  if (!product) return <p>   </p>;

  return (
    <div className="productPage container">
      <Breadcrumbs
        paths={[
          { name: "", path: "/" },
          { name: "", path: "/catalog" },
          { name: product?.category || "" },
          { name: product?.name || "" },
        ]}
      />

      <div className="productPage__content">
        <h1>{product.name}</h1>
        <p>: {product.category}</p>

        <img
          src={`${process.env.PUBLIC_URL}/${product.imgProductRef}`}
          alt={product.name}
          onError={(e) =>
            (e.currentTarget.src = `${process.env.PUBLIC_URL}/images/no-image.png`)
          } //    ,  
        />
        <p>{product.ageCategory}</p>
        <p className="productPage__price">{product.priceRegular} </p>
      </div>
    </div>
  );
};
